@font-face {
    font-family: 'ubuntu';
    src: url('../fonts/ubuntu-regular.ttf');
}

@font-face {
    font-family: 'ubuntu-bold';
    src: url('../fonts/ubuntu-bold.ttf');
}

body {
    padding: 0;
    margin: 0;
    font-family: ubuntu;
}

.padding-top{
    padding-top: 60px;
}

.vh-min-100{
    min-height: 100vh !important;
}

/* Override the default bootstrap behavior where horizontal description lists 
   will truncate terms that are too long to fit in the left column 
*/
.dl-horizontal dt {
    white-space: normal;
}

li.active:last-child a {
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
}


/*nav styles*/

.navbar-nav {
    margin: 0 -15px;
}

.navbar {
    backdrop-filter: saturate(150%) blur(10px);
    background-color: rgba(255,255,255, 0.4);
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
    box-shadow: 1px 1px 3px rgba(33, 33, 33, 0.1);
}

.navbar-nav .nav-item {
    color: rgba(33, 33, 33, 0.4) !important;
    font-size: 1.1rem;
    text-decoration: none !important;
    padding: 6px 12px 6px 12px;
    cursor: pointer;
    font-family: ubuntu-bold;
}

.navbar-nav .nav-item.active {
    color: rgba(33, 33, 33, 1.0) !important;
    background-color: rgba(33, 33, 33, .055) !important;
    border-radius: 16px;
}

.navbar-nav .nav-item:hover{
    color: #212121 !important;
}

.navbar-toggler .nav-icons span:nth-child(2){
    max-width: 17px;
}

.navbar-toggler{
    margin-bottom: 0; 
    border: 0 !important;
}

.navbar-toggler .nav-icons .icon-bar {
    height: 3px;
    width: 30px;
    margin-bottom: 5px;
    background-color: rgba(33, 33, 33, 1);
    border-radius: 50%;
    box-shadow: 2px 2px 4px rgba(33, 33, 33, 0.3);
}

.navbar-brand{
    font-weight: 900;
    font-size: 1.5rem;
}


/* Button Styles */
.btn-outline {
    border-color: #212121;
    color: #212121;
}

.btn-primary {
    background-color: #212121;
    border: 1px solid rgba(33,33,33, 0.2);
}

.btn-primary:hover, .btn-outline:hover{
    background-color: #eeeeee;
    color: #212121;
}

/*
 *Common Styles
*/

.carousel-indicators li{
    border-radius: 50%;
    background-color: rgba(33, 33, 33, 1) !important;
}

.col-md-4 {
    padding: 0;
}


@media only screen and (min-width: 768px) {

    /*nav styles*/
    .navbar.fixed-top {
        padding-left: 16px;
    }

    .navbar-nav {
        padding-right: 24px;
    }
}