.section-container {
    display: flex;
    padding-top: 80px;
    background-color: white !important; 
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}

.section-container > div {
    margin: 0 16px 32px 16px;
}


/* Card styles */
.card{
    border-radius: 10px 30px 10px 30px;
}

.card-body{
    background-color: #fff !important;
    border-color: rgba(33, 33, 33, .1);
    border-radius: 0 0 10px 30px;
}

.card .card-header{
    background-color: rgba(33, 33, 33, .033) !important;
    border-bottom: 1px solid rgba(33, 33, 33, .1) !important;
    border-radius: 10px 30px 0 0;
    color: #212121;
    font-weight: bold;
    font-size: 1.2em;  
}

/* Video Styles and Image styles*/
.section-container > .video {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

/*To not get complete width while loading*/
video, .images {
    max-width: 250px;
}

.video, .images-main{
    position: relative;
}


.section-container > .images-main > .bg-1 > .images img {
    height: 520px;
    margin: auto;
}

.section-container > .images-main > .bg-1 > .images {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
    overflow-x: hidden;
    border-radius: 22px;
}

.section-container > .video > video {
    height: 520px;
    max-height: 520px;
}

.section-container > .images-main {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 534px;
}

.video {
    position: relative; 
}

.section video, .section .images-main {
    background-color: rgba(255, 255, 255, 0.1) !important;
}

.images-main, .card, .video {
    box-shadow: 0.1px 0.1px 5px rgba(33, 33, 33, 0.1),
        -0.1px -0.1px 5px rgba(33, 33, 33, 0.1)
}

.images-main, .video {
    border-radius: 30px;
}

video {
    height: 520px;
    border-radius: 20px;
}

.images-main, .images {
    height: 520px;
    border-radius: 30px;
}


/* Mobile items */

.notch {
    width: 90px;
    height: 17px;
    position: absolute;
    background-color: #212121;
    z-index: 15;
    border-radius: 0 0 30px 30px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding-bottom: 5px;
}

.speaker {
    width: 25px;
    height: 3px;
    border-radius: 3px;
    background-color: rgba(255, 255, 255, 1.0);
    margin-right: 7px;
}

.sensor {
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 1.0);
}

.left-btns {
    position: absolute;
    left: -5px;
    top: 10%;
}

.right-btns {
    position: absolute;
    right: -5px;
    top: 20%;
}

.left-btn1 {
    background-color: #212121;
    width: 5px;
    height: 20px;
    border-radius: 3px 0 0 3px;
    margin-bottom: 20px;
}

.left-btn2, .left-btn3 {
    background-color: #212121;
    width: 5px;
    height: 35px;
    border-radius: 3px 0 0 3px;
    margin-bottom: 10px;
}

.left-btn1, .left-btn2, .left-btn3 {
    box-shadow: -3px -3px 4px rgba(255, 255, 255, 0.4);
}

.bg-1, .right-btn1 {
    box-shadow: 2px 2px 5px rgba(33, 33, 33, 0.4);
}

.right-btn1 {
    background-color: #212121;
    width: 5px;
    height: 70px;
    border-radius: 0 3px 3px 0;
}


.bg-1 {
    border: 9px solid #212121;
    border-radius: 30px;
    display: flex;
    justify-content: center;
}

.button-left {
    background: rgba(255, 255, 255, 0.7);
    color: #212121;
    z-index: 15;
    font-size: 1.5rem;
    border-radius: 0 30px 30px 0;
    opacity: 0.7;
    padding: 8px;
    position: absolute;
    left: -5px;
}

.button-right {
    background: rgba(255, 255, 255, 0.7);
    color: #212121;
    position: absolute;
    right: -5px;
    z-index: 15;
    padding: 8px;
    font-size: 1.5rem;
    border-radius: 30px 0 0 30px;
    opacity: 0.7;
}

.button-left:hover, .button-right:hover{
    opacity: 1.0;
}

.profile-photo {
    display: block;
    margin: auto;
    width: 200px;
    height: 200px;
    border-radius: 100px;
    object-fit: cover;
    border: 1px solid #212121;
    box-shadow: 0 4px 8px 0 #7fbaae;
}

.notch-left{
    background-color: #212121;
    width: 15px;
    height: 10px;
    top: -4px;
    left: -4px;
    position: absolute;
    border-radius: 50%;
}


